body.mobile-nav-active .main-navigation {
	overflow-y: scroll;
	opacity: 1;
}

.main-navigation {
	position: fixed;
	z-index: -1;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	width: 100vw;
	height: 100vh;
	overflow-x: hidden;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;
	padding: 0 0 2rem 29vw;
	background: $color__brand-1;
	opacity: 0;
	transition: opacity 0.25s;

	body.mobile-nav-active & {
		overflow-y: scroll;
		opacity: 1;
		z-index: 10;
	}

	> ul {
		display: flex;

		> li {
			margin-left: 1.5rem;

			> a {
				font-family: $font__med;
			}
		}
	}

	ul {
		list-style: none;
		margin: 0;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			position: absolute;
			top: 100%;
			left: -999em;
			z-index: 99999;

			ul {
				left: -999em;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}

			:hover > a,
			.focus > a {
			}

			a:hover,
			a.focus {
				background: $color__brand-1;
				color: #fff;
			}

			.current_page_item,
			.current-menu-item {
				a:hover,
				a.focus {
					background: #FFF;
					color: $color__brand-3;
				}
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;

		&:hover > a,
		&.focus > a {
		}
	}

	a {
		display: block;
		text-decoration: none;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
		color: $color__brand-3;
	}
}

.menu-toggle {
	padding-bottom: 2rem;
	color: $color__brand-1;
	position: relative;
	border: none;
	outline: none;
	background: none;
	font-size: 1.375em;
	line-height: 1;
	margin-left: auto;
	@include margin-right;

	&:hover {
		cursor: pointer;
		background: none;
	}

	&:before,
	.inner:after,
	.inner:before {
		content: '';
		display: block;
		background-color: $color__brand-1;
		height: 4px;
		width: 25px;
		position: absolute;
		top: 0;
		right: 0;
		transition: transform .125s, top .5s, opacity .25s;
		transform-origin: center center;
	}

	.inner:before {
		top: 9px;
	}

	.inner:after {
		top: 18px;
	}

	&.nav-open {
		&:before {
			transform: rotate(45deg);
			top: 10px;
		}

		.inner:before {
			opacity: 0;
		}

		.inner:after {
			transform: rotate(-45deg);
			top: 10px;
		}
	}

	@include respond-to(large) {
		display: none;
	}
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

@media screen and (min-width: 37.5em) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}
